// 1. Identifica o iframe como o elemento de origem
const portalEls = document.querySelectorAll('iframe.portal');
portalEls.forEach(iframeEl => {
    const overlayEl = iframeEl.closest('.portal-parent').querySelector('.portal-overlay');
    overlayEl.addEventListener('click', async () => {
        iframeEl.style.viewTransitionName = 'transicao-expandir';

        const transition = document.startViewTransition(async () => {
            // 2. Remove o nome do iframe (ele vai sumir da página 1)
            iframeEl.style.viewTransitionName = '';
            
            // 3. Executa a lógica de navegação/troca de rota da sua SPA
            location.href = iframeEl.src 

            // 4. Identifica o novo container da Página 2 como o destino
            // Pode ser o body ou a div principal da nova rota
            document.querySelector('body').style.viewTransitionName = 'transicao-expandir';
        });

        // 5. Limpeza: Após a transição, remova o nome para evitar conflitos futuros
        transition.finished.then(() => {
            const el = document.querySelector('body');
            if (el) el.style.viewTransitionName = '';
        });
    });
});